import React, {useState, useEffect} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {Card, Upload, message, Row, Col, Button, Image, Tooltip} from 'antd'
import Utils from "../../../../utils";
import {DeleteOutlined, InboxOutlined, PlusCircleOutlined, UploadOutlined, FullscreenOutlined} from "@ant-design/icons";
import {_deleteImagesRoom} from "../../../../redux/actions";

const {Dragger} = Upload;
const PhotosFields = (props) => {
    let {_deleteImagesRoom} = props

    const beforeUpload = file => {
        const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/jpg';
        if (!isJpgOrPng) {
            message.error('You can only upload JPG/PNG file!');
        }
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isLt2M) {
            message.error('Image must smaller than 2MB!');
        }
        return isJpgOrPng && isLt2M;
    }


    const onDelete = (imageId) => {
        console.log({imageId})
        if (props?.mode === "UPDATE") {
            _deleteImagesRoom(props?.data?._id, imageId)
        }
    }

    const [visible, setVisible] = useState(false);
    const [newImages, setNewImages] = useState([])
    const onUploadChange = (info) => {
        let {fileList} = info
        let key = 'updatable'
        if (typeof (fileList) !== "undefined" && Array.isArray(fileList)) {
            let NewImg = []
            for (let i = 0; i < fileList.length; i++) {
                Reflect.set(fileList[i], "status", "done")

                message.success({content: "Successfully!", key, duration: 2})
                Utils.getBase64(fileList[i].originFileObj, imageUrl => {
                    NewImg.push(imageUrl)
                });
            }
            if(Array.isArray(newImages) && newImages.length > 0){
                for(let i=0;i<newImages.length;i++){
                    NewImg.push(newImages[i])
                }
            }


            setNewImages(NewImg)
        }
    }
    const onVisible = () => {
        setVisible(!visible)
    }

    return (
        <Card title={props?.mode !== "UPDATE" ? 'Upload Photo Rooms' : "Gallery Photos"}
              extra={<>
                  {
                      props?.mode === "UPDATE" ? (
                          <div className={'flex items-center gap-2'}>
                              <Tooltip title={'Add Photos to Gallery'}>
                                  <Upload {...Utils.UploadProps({
                                      multiple: true, listType: "picture",
                                      progress: {
                                          strokeColor: {
                                              '0%': '#108ee9',
                                              '100%': '#87d068',
                                          },
                                          strokeWidth: 3,
                                          format: percent => percent && `${parseFloat(percent.toFixed(2))}%`,
                                      }
                                  })} fileList={[]}
                                          beforeUpload={beforeUpload}
                                          onChange={e => {
                                              onUploadChange(e)
                                              props.handleUploadChange(e)
                                          }}
                                  >
                                      <Button>
                                          <UploadOutlined/> Upload
                                      </Button>
                                  </Upload>
                              </Tooltip>
                              <Tooltip title={'Open in Modal'}>
                                  <Button
                                      type={'primary'}
                                      ghost
                                      onClick={onVisible}
                                      icon={<FullscreenOutlined/>}/>
                              </Tooltip>

                          </div>
                      ) : null
                  }

              </>}

        >
            <div className="clearfix">

                {
                    props?.mode !== "UPDATE" && (
                        <Upload {...Utils.UploadProps({
                            multiple: false, listType: "picture",
                            progress: {
                                strokeColor: {
                                    '0%': '#108ee9',
                                    '100%': '#87d068',
                                },
                                strokeWidth: 3,
                                format: percent => percent && `${parseFloat(percent.toFixed(2))}%`,
                            }
                        })} fileList={props?.photos ?? []}
                                beforeUpload={beforeUpload} onChange={e => props.handleUploadChange(e)}
                        >
                            <Button>
                                <UploadOutlined/> Upload
                            </Button>
                        </Upload>
                    )
                }
                {
                    typeof (props?.mode) !== "undefined" &&
                    typeof (props?.data) !== "undefined" &&
                    props?.mode === "UPDATE" && (
                        <div className={'w-full flex justify-start flex-wrap gap-4 '}>
                            {
                                newImages.map((item, index) => (
                                    <div className={'w-[220px] md:w-[320px]'} key={`image-item-${index}`}>
                                        <div className="relative rounded-[20px] h-full w-full overflow-hidden">
                                            <div className={'absolute top-2 right-3 z-10'}>
                                                <Button
                                                    type={'primary'}
                                                    loading={false}
                                                    size={'small'}
                                                >NEW</Button>
                                            </div>
                                            <img
                                                src={item}
                                                alt={item}
                                                className={'w-full h-[180px] md:h-[250px] object-cover'}
                                            />
                                        </div>
                                    </div>
                                ))
                            }
                            {
                                typeof (props?.data?.images) !== "undefined" &&
                                Array.isArray(props?.data?.images) &&
                                props?.data?.images.length > 0 &&
                                props?.data?.images.map((item, index) => (
                                    <div className={'w-[220px] md:w-[320px]'} key={`image-item-${index}`}>
                                        <div className="relative rounded-[20px] h-full w-full overflow-hidden">
                                            <div className={'absolute top-2 right-3 z-10'}>
                                                <Button
                                                    onClick={() => onDelete(item?._id)}
                                                    loading={item?.loading ?? false}
                                                    size={'small'}
                                                    icon={<DeleteOutlined/>}/>
                                            </div>
                                            <img
                                                src={[item?.prefix_url, item?.filename].join("/")}
                                                alt={[item?.prefix_url, item?.filename].join("/")}
                                                className={'w-full h-[180px] md:h-[250px] object-cover'}
                                            />
                                            {/*<Image*/}
                                            {/*    style={{*/}
                                            {/*        height:250,*/}
                                            {/*        width:"100%",*/}
                                            {/*        borderRadius:20,*/}
                                            {/*        objectFit:'cover'*/}
                                            {/*    }}*/}
                                            {/*    src={[item?.prefix_url,item?.filename].join("/")}*/}
                                            {/*/>*/}
                                        </div>
                                    </div>
                                ))
                            }


                        </div>
                    )
                }
                <div style={{display: 'none'}}>
                    {
                        typeof (props?.mode) !== "undefined" &&
                        typeof (props?.data) !== "undefined" &&
                        props?.mode === "UPDATE" && typeof (props?.data?.images) !== "undefined" && Array.isArray(props?.data?.images) && props?.data?.images.length > 0 && (
                            <Image.PreviewGroup preview={{visible, onVisibleChange: vis => setVisible(vis)}}>
                                {
                                    Array.isArray(props?.data?.images) && props?.data?.images.length > 0 &&
                                    props?.data?.images.map((item) => (
                                        <Image src={[item?.prefix_url, item?.filename].join("/")}/>
                                    ))
                                }
                            </Image.PreviewGroup>
                        )
                    }
                </div>


            </div>
        </Card>
    )
}
PhotosFields.propTypes = {
    _deleteImagesRoom: PropTypes.func.isRequired
}
PhotosFields.defaultProps = {}

export default connect(({Room}) => {
    let {data, loading} = Room
    return {data, loading}
}, {_deleteImagesRoom})(PhotosFields)
