import React, {useState, useEffect} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import PageHeaderAlt from 'components/layout-components/PageHeaderAlt'
import {Tabs, Form, notification, Button, message, Progress} from 'antd';
import Flex from 'components/shared-components/Flex'
import GeneralField from './GeneralField'
import PhotosFields from './PhotosFields'
import FacillityFields from './FacillityFields'
import Utils from "../../../../utils";
import ApiService from "../../../../services/Api.service";
import {useLocation} from "react-router-dom";
import AxiosInstance, {ROOT_API} from "../../../../utils/axios-instance";
import axios from "axios";

const {TabPane} = Tabs;

const getBase64 = (img, callback) => {
    const reader = new FileReader();
    reader.addEventListener('load', () => callback(reader.result));
    reader.readAsDataURL(img);
}

const ADD = 'ADD'
const EDIT = 'EDIT'

const RoomsForm = props => {

    const {mode = ADD, param, data} = props
    const locations = useLocation()

    const [form] = Form.useForm();
    const [photos, setPhotos] = useState([])
    const [uploadedImg, setImage] = useState('')
    const [uploadLoading, setUploadLoading] = useState(false)
    const [submitLoading, setSubmitLoading] = useState(false)

    useEffect(() => {
        if (mode === 'UPDATE') {
            form.setFieldsValue({
                name: data?.name ?? "",
                description: data?.description ?? "",
                limit: data?.limit ?? 4,
                currency: data?.currency ?? "IDR",
                unit: data?.unit ?? "day",
                facility: {
                    ...data?.facility,
                },
                price: {
                    ...data?.price,
                }
            });
            // setImage(product.image)
        }
    }, [form, mode, param, props]);

    const [progress, setProgress] = useState(0)

    const handleUploadChange = info => {
        let {fileList} = info
        let key = 'updatable'
        message.loading({content: "loading...", key, duration: 1})
        if (props?.mode === "UPDATE") {
            console.log({mode: props?.mode, fileList, info})
            if (typeof (fileList) !== "undefined" && Array.isArray(fileList)) {
                for (let i = 0; i < fileList.length; i++) {
                    Reflect.set(fileList[i], "status", "done")

                    message.success({content: "Successfully!", key, duration: 2})
                    getBase64(fileList[i].originFileObj, imageUrl => {
                        setImage(imageUrl)
                        setUploadLoading(true)
                    });
                }
            }
            setPhotos(fileList)
        } else {
            if (typeof (fileList) !== "undefined" && Array.isArray(fileList)) {
                for (let i = 0; i < fileList.length; i++) {
                    Reflect.set(fileList[i], "status", "done")

                    message.success({content: "Successfully!", key, duration: 2})
                    getBase64(fileList[i].originFileObj, imageUrl => {
                        setImage(imageUrl)
                        setUploadLoading(true)
                    });
                }
            }
            setPhotos(fileList)
        }

    };

    const onFinish = () => {
        let key = "updatable"
        setSubmitLoading(true)
        message.loading({content: "Loading...", key})
        form.validateFields().then(async values => {
                if (props?.mode === "UPDATE") {
                    notification.warn({
                        message: 'process save to database',
                        description: 'please don\'t exit/reload this page',
                        duration: 2
                    });
                    await new ApiService({
                        url: `/api/v1/villa/self/rooms/${param}`,
                        body: values,

                    }).patch()
                        .then(async (result) => {

                                let {data, error} = result
                                const formdata = new FormData()
                                for (let i = 0; i < photos.length; i++) {
                                    formdata.append(`files`, photos[i].originFileObj)

                                }
                                await new ApiService({
                                    url: `/api/v1/villa/self/rooms/update/photo/${data?._id}`,
                                    body: formdata,
                                    config: {
                                        headers: {
                                            "Authorization": `Bearer ${Utils.getToken('get')}`,
                                            "Content-Type": "application/formdata"
                                        },
                                        onUploadProgress: progressEvent => {
                                            const totalLength = progressEvent.lengthComputable ? progressEvent.total : progressEvent.target.getResponseHeader('content-length') || progressEvent.target.getResponseHeader('x-decompressed-content-length');
                                            const val = (progressEvent.loaded / progressEvent.total) * 100;
                                            console.log("onUploadProgress", totalLength, {progressEvent});

                                            if (val >= 100) {
                                                setTimeout(() => {
                                                    setProgress(0)
                                                }, 2000)
                                            }else{
                                                setProgress(val)
                                            }
                                        }
                                    }
                                })
                                    .put()
                                    .then(({data}) => {
                                        message.success({
                                            content: data?.message ?? result?.message ?? "Successfully!",
                                            key,
                                            duration: 2
                                        })
                                        notification.success({
                                            message: 'Success save to database',
                                            description: 'the data has been successfully updated',
                                            duration: 2
                                        });
                                    })
                                    .catch((err) => {
                                        message.error({
                                            content: err?.message ?? result?.message ?? "Successfully!",
                                            key,
                                            duration: 2
                                        })
                                    })
                            }
                        )
                        .catch((err) => {
                            message.error({content: err?.message ?? "Some Error!", key, duration: 2})
                        })
                } else {
                    await new ApiService({
                        url: '/api/v1/villa/self/rooms/create',
                        body: values,
                    }).post()
                        .then(async (result) => {
                            let {data, error} = result
                            const formdata = new FormData()
                            for (let i = 0; i < photos.length; i++) {
                                formdata.append(`files`, photos[i].originFileObj)
                            }
                            await new ApiService({
                                url: `/api/v1/villa/self/rooms/update/photo/${data?._id}`,
                                body: formdata,
                                config: {
                                    headers: {
                                        "Authorization": `Bearer ${Utils.getToken('get')}`,
                                        "Content-Type": "application/formdata"
                                    }
                                }

                            }).put()
                                .then((response) => {
                                    message.success({
                                        content: response?.message ?? result?.message ?? "Successfully!",
                                        key,
                                        duration: 2
                                    })
                                })
                                .catch((err) => {
                                    message.error({
                                        content: err?.message ?? result?.message ?? "Successfully!",
                                        key,
                                        duration: 2
                                    })
                                })
                        })
                        .catch((err) => {
                            message.error({content: err?.message ?? "Some Error!", key, duration: 2})
                        })
                }
                setSubmitLoading(false)
            }
        ).catch(info => {
            setSubmitLoading(false)
            console.log('info', info)
            message.error('Please enter all required field ');
        });
    };

    const [tabs, setTab] = useState(() => {
        let tab = new URLSearchParams(locations.search).get("tab")
        if (tab) {
            return tab.toString()?.toLowerCase()
        }
        return "general"
    })

    function setUp(value) {
        if (window) {
            let prefix = "?tab="
            let newUrl = [window.origin, locations.pathname].join("")
            window.history.pushState({article: 'read-all'}, 'Read All Article', [newUrl, prefix, value].join(""));
        }
    }


    function onTabs(value) {
        setUp(value)
    }

    return (
        <>
            <Form
                layout="vertical"
                form={form}
                initialValues={{
                    name: "",
                    description: '',
                    limit: 1,
                    currency: "IDR",
                    unit: "day",
                    facility: {
                        ac: false,
                        tv: false,
                        wifi: false,
                        smooking: false,
                        bed_type: null,
                        other: ""
                    },
                    price: {
                        regular: 0,
                        special: 0,
                        discount: 0
                    }
                }}
                name="advanced_search"
                className="ant-advanced-search-form"
            >
                <PageHeaderAlt className="border-bottom" overlap>
                    <div className="container">
                        <Flex className="py-2" mobileFlex={false} justifyContent="between" alignItems="center">
                            <h2 className="mb-3">{mode === 'ADD' ? 'Add New Product' : `Edit Product`} </h2>
                            <div className="mb-3">
                                <Button className="mr-2">Discard</Button>
                                <Button type="primary" onClick={() => onFinish()} htmlType="submit"
                                        loading={submitLoading}>
                                    {mode === 'ADD' ? 'Add' : `Save`}
                                </Button>
                            </div>
                        </Flex>
                    </div>
                </PageHeaderAlt>
                <div className="container">
                    <Tabs defaultActiveKey={tabs} onChange={onTabs} style={{marginTop: 30}}>
                        <TabPane tab="General" key="general">
                            <GeneralField/>
                        </TabPane>
                        <TabPane tab="Photos" key="photos">
                            <PhotosFields
                                {...props}
                                photos={photos}
                                setPhotos={setPhotos}
                                uploadedImg={uploadedImg}
                                uploadLoading={uploadLoading}
                                handleUploadChange={handleUploadChange}
                            />
                        </TabPane>
                        <TabPane tab="Facility" key="facility">
                            <FacillityFields {...props} form={form}/>
                        </TabPane>
                    </Tabs>
                </div>
            </Form>
        </>
    )
}

RoomsForm.propTypes = {}
RoomsForm.defaultProps = {}

export default connect(({Room}) => {
    let {data, loading} = Room
    return {data, loading}
}, {})(RoomsForm)
