import React, {useEffect} from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import {_getRoomsDetail} from "../../../../redux/actions";
import {useParams} from "react-router-dom";
import RoomsForm from "../rooms-form";

const RoomsUpdate = (props)=> {
    let { _getRoomsDetail } = props
    let { id } = useParams()

    useEffect(()=> {
        _getRoomsDetail(id,{
            type:"update"
        })
    },[_getRoomsDetail])
    return (
        <div className="w-full">
            <RoomsForm mode={"UPDATE"} param={id}/>
        </div>
    )
}

RoomsUpdate.propTypes = {
    _getRoomsDetail: PropTypes.func.isRequired
}
RoomsUpdate.defaultProps = {}

export default connect(({Room})=> {
    let { data ,loading } = Room
    return { data ,loading}
},{_getRoomsDetail})(React.memo(RoomsUpdate))
