import React from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {Card, Form, Switch, Row, Col, Input, Select} from 'antd'
import {CheckOutlined, CloseOutlined} from '@ant-design/icons'
import ReactQuill from "react-quill";
import Flex from "../../../../components/shared-components/Flex";

const FacillityFields = (props) => {

    const [fac,setFac] = React.useState(()=> {
        if(typeof(props?.form?.getFieldValue(['facility'])) !== "undefined"){
            return {
                ...props?.form?.getFieldValue(['facility']),
            }
        }
        return {
            ac:false,
            other:""
        }
    })

    const [options] = React.useState({
        theme: "snow",
        style: {height: 240},
        placeholder: 'Input your another facility...',
        readOnly: false,
        debug: 'info',
        toolbar: [
            // [{ header: [1, 2, false] }],
            [{'size': ['small', false, 'large', 'huge']}],  // custom dropdown
            // [{ 'script': 'sub'}, { 'script': 'super' }],      // superscript/subscript
            // [{ 'indent': '-1'}, { 'indent': '+1' }],          // outdent/indent
            // [{ 'direction': 'rtl' }],                         // text direction
            // ['blockquote', 'code-block'],
            // ['bold', 'italic', 'underline', 'strike'],        // toggled buttons

            [{'list': 'ordered'}, {'list': 'bullet'}],

            [{'color': []}, {'background': []}],          // dropdown with defaults from theme
            [{'font': []}],
            [{'align': []}],

            ['clean']                                         // remove formatting button
        ],
        value:fac?.other ?? ""
    })

    return (
        <div className="mail-compose">
            <Card bordered={false}>

                <Row gutter={24}>
                    <Col sm={24}>
                        <Row gutter={24}>
                            <Col sm={12} lg={8} xl={4}>
                                <Form.Item name={['facility', 'ac']} label={'AC'}>
                                    <Switch
                                        style={{padding: 0, margin: 0, height: "auto"}}
                                        size={'small'}
                                        defaultChecked={fac?.ac ?? false}
                                        checkedChildren={<CheckOutlined/>}
                                        unCheckedChildren={<CloseOutlined/>}
                                    />
                                </Form.Item>

                            </Col>
                            <Col sm={12} lg={8} xl={4}>
                                <Form.Item label={'TV'} name={['facility', 'tv']}>
                                    <Switch
                                        defaultChecked={fac?.tv ?? false}
                                        checkedChildren={<CheckOutlined/>}
                                        unCheckedChildren={<CloseOutlined/>}
                                    />
                                </Form.Item>
                            </Col>
                            <Col sm={12} lg={8} xl={4}>
                                <Form.Item label={'Wifi'} name={['facility', 'wifi']}>
                                    <Switch
                                        defaultChecked={fac?.wifi ?? false}
                                        checkedChildren={<CheckOutlined/>}
                                        unCheckedChildren={<CloseOutlined/>}
                                    />
                                </Form.Item>
                            </Col>
                            <Col sm={12} lg={8} xl={4}>
                                <Form.Item label={'Smooking'} name={['facility', 'smooking']}>
                                    <Switch
                                        defaultChecked={fac?.smoking ?? fac?.smooking ?? false}
                                        checkedChildren={<CheckOutlined/>}
                                        unCheckedChildren={<CloseOutlined/>}
                                    />
                                </Form.Item>
                            </Col>
                        </Row>
                    </Col>

                    <Col sm={8}>
                        <Form.Item label={'Bed Type'} name={['facility', 'bed_type']}>
                            <Select>
                                {["single","double","large","extra-large"].map((item, index) => (
                                    <Select.Option value={item} key={item}>{item}</Select.Option>
                                ))}

                            </Select>
                        </Form.Item>
                    </Col>
                    <Col sm={24}>
                        <Form.Item label={'Other'} name={['facility', 'other']} style={{minHeight: 300}}>

                            <ReactQuill {...options}/>
                        </Form.Item>

                    </Col>
                </Row>
            </Card>
        </div>
    )
}

FacillityFields.propTypes = {}
FacillityFields.defaultProps = {}

export default FacillityFields
